
@charset "UTF-8";
body,
div,
span,
header,
footer,
nav,
section,
aside,
article,
ul,
dl,
dt,
dd,
li,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
i,
b,
textarea,
button,
input,
select,
figure,
figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "PingFang-SC-Medium","Microsoft Yahei",sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  font-size: .32rem;
  box-sizing: border-box;
}
body:focus,
div:focus,
span:focus,
header:focus,
footer:focus,
nav:focus,
section:focus,
aside:focus,
article:focus,
ul:focus,
dl:focus,
dt:focus,
dd:focus,
li:focus,
a:focus,
p:focus,
h1:focus,
h2:focus,
h3:focus,
h4:focus,
h5:focus,
h6:focus,
i:focus,
b:focus,
textarea:focus,
button:focus,
input:focus,
select:focus,
figure:focus,
figcaption:focus {
  outline: none;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0.013333rem transparent;
  border-radius: 0.133333rem;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 0.133333rem;
  -webkit-box-shadow: inset 0 0 0.08rem rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html,
body {
  height: 100%;
  width: 100%;
  background-color: #F5F5F5;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.clear {
  zoom: 1;
}
.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.margin {
  margin: 0 auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*
  作用： 文本超出变省略号(两行)
  用法： 给当前元素，加入ellipsis-2的类名
*/
.ellipsis-2 {
  display: -webkit-box;
  overflow: hidden;
  white-space: normal !important;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.paddingTop {
  padding-top: 1.95rem;
}
.vm-progress-circle__path {
  -webkit-transform-origin: center !important;
          transform-origin: center !important;
  -webkit-transform: rotate(-90deg) !important;
          transform: rotate(-90deg) !important;
}
.vm-progress__text {
  font-size: 0.22rem !important;
  font-weight: 800;
}
.mint-toast-text {
  color: #fff !important;
}
#app {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  position: relative;
}
.my-toast {
  background-color: #fff;
  padding: 0.16rem 0.4rem !important;
  background-color: #000;
}
.my-toast span {
  color: #fff;
}


